import React from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import {
  userDetails,
  UserUpdateStatus,
} from "../apiServices/home/homeHttpService";
import { showAlert } from "./commonComponent/alertManager";
import { useAlert } from "react-alert";

function UserDetails() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();
  const alert = useAlert();

  const { data: details, refetch } = useQuery({
    queryKey: ["usersDetail", id],
    queryFn: async () => {
      return userDetails(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.user,
  });

  const changeStatus = async (v) => {
    const formData = {
      status: v,
    };
    const response = await UserUpdateStatus(id, formData);
    if (!response.error) {
      showAlert(alert, response.message, { timeout: 3000 });
      refetch();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  return (
    <>
      <Header />
      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <Link
            to={`/admin/users-management?page=${queryParams.get("page") || 1}`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <div className="row">
                        <div className="col-lg-9 col-md-8 col-12 order-md-0 order-1">
                          <div className="mt-4 user-view-wrapper">
                            <div className="mt-2">
                              <strong>Name</strong>
                              <span>{details?.name}</span>
                            </div>
                            <div className="mt-2">
                              <strong>Email</strong>
                              <span>{details?.email}</span>
                            </div>
                            <div className="mt-2">
                              <strong>Mobile No.</strong>
                              <span>{details?.mobileNumber}</span>
                            </div>
                            <div className="mt-2">
                              <strong>Cuisines</strong>
                              {details?.prefers?.join(", ")}
                            </div>
                            <div className="mt-2">
                              <strong>Status </strong>

                              <span
                                className={
                                  details?.status
                                    ? "status_accepted"
                                    : "status_declained"
                                }
                              >
                                {details?.status ? "Active" : "Inactive"}
                              </span>
                            </div>
                            <div className="mt-2">
                              <strong>Rating </strong>
                              <span className="ps-1">
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star main-color" />
                                <i className="fa fa-star " />
                              </span>
                            </div>
                            <div className="mt-2">
                              <strong>Review </strong>
                              <span>Good service</span>
                            </div>
                            <div className="d-flex mt-2">
                              <strong>Status Update </strong>
                              <span>
                                <div className="ms-2 form-check form-switch custom-switch">
                                  <input
                                    className="form-check-input "
                                    type="checkbox"
                                    onChange={(e) =>
                                      changeStatus(e.target.checked)
                                    }
                                    checked={details?.status}
                                  />
                                  <label
                                    className="form-check-label"
                                    htmlFor="flexSwitchCheckChecked"
                                  />
                                </div>
                              </span>
                            </div>
                            <div className="mt-2">
                              <strong>Biography</strong>
                              <span>{details?.bio}</span>
                            </div>
                          </div>
                        </div>
                        <div className="col-lg-3 col-md-4 col-12 order-lg-0 mt-4 mt-md-0 order-0">
                          <div className="d-flex justify-content-center justify-content-md-end">
                            <div className="user-img-wrapper">
                              <img
                                src={
                                  details?.profile
                                    ? details?.profile
                                    : "../../assets/img/user1.png"
                                }
                                alt=""
                                className="w-100 h-100"
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default UserDetails;
